<script lang="ts">
  import { onMount } from 'svelte'
  import { ethers } from 'ethers'
  import { isConnect, signer } from '@/store/index'
  import mdmABI from '@/abi/MetaDriveMotorcycle.json'

  const mdmCA = '0xA24Af95aaDd25acA8AC4Be629B1C032f476CD385'
  let remainCount = 0
  let thisProvider
  onMount(async () => {
    thisProvider = new ethers.providers.JsonRpcProvider('https://polygon-rpc.com')
    remainCount = await remainSupply(thisProvider)
  })

  const regex = /(\s*)/g
  let loopObjArr = []
  let addressArray = []
  let amountArray = []
  let address = ''
  let amount = ''
  let totalAirdropCountNumber = 0

  async function transfer() {
    const mdmContract = new ethers.Contract(mdmCA, mdmABI, $signer)
    try {
      const transaction = await mdmContract.listAirdrip(addressArray, amountArray)
      await transaction.wait()
      init()
      alert('Success!')
    } catch (e) {
      if (e.code === 4001) {
        alert('트랜잭션이 취소되었습니다.')
      } else if (e.code === -32603) {
        alert('에어드랍용 지갑이 아닙니다.')
      } else {
        alert('Error!')
      }
    }
  }

  async function remainSupply(provider) {
    const mdmContract = new ethers.Contract(mdmCA, mdmABI, provider)
    const supply = await mdmContract.getCurrentAirdropCount()
    return 1500 - supply + 1
  }

  async function init() {
    loopObjArr = []
    addressArray = []
    amountArray = []
    address = ''
    amount = ''
    totalAirdropCountNumber = 0
    remainCount = await remainSupply(thisProvider)
  }

  function totalAirdropCount() {
    totalAirdropCountNumber = 0
    for (let i = 0; i < amountArray.length; i++) {
      totalAirdropCountNumber += parseInt(amountArray[i])
    }
  }

  function enroll() {
    loopObjArr = []
    inputAddress()
    inputAmount()
    for (let i = 0; i < addressArray.length; i++) {
      loopObjArr.push({
        address: addressArray[i],
        amount: amountArray[i],
      })
    }
    totalAirdropCount()
    console.log(loopObjArr)
  }

  function inputAddress() {
    addressArray = []
    const nonSpaceAddresses = address.replace(regex, '')
    nonSpaceAddresses.split(',').forEach((item) => {
      addressArray.push(item)
    })
  }

  function inputAmount() {
    amountArray = []
    const nonSpaceAmount = amount.replace(regex, '')
    nonSpaceAmount.split(',').forEach((item) => {
      amountArray.push(item)
    })
    if (amountArray.length === 1) {
      for (let i = 0; i < addressArray.length - 1; i++) {
        amountArray.push(amountArray[0])
      }
    }
  }
</script>

{#if $isConnect === true}
  <div class="box">
    <div class="remain-count">
      남은 에어드랍: {remainCount} 개
    </div>
  </div>
  <div class="box">
    <div class="address-input">
      <label for="address">Address</label>
      <input type="text" id="address" bind:value={address} />
    </div>
    <div class="amount-input">
      <label for="amount">Amount</label>
      <input type="text" id="amount" bind:value={amount} />
    </div>
    <div class="send-button">
      <button on:click={enroll}>등록</button>
      <button on:click={transfer}>보내기</button>
    </div>
  </div>

  <div class="box">
    <div class="count">
      등록된 총 지갑 수: {loopObjArr.length}
    </div>
    <div class="count">
      등록된 총 nft 수: {totalAirdropCountNumber}
    </div>
  </div>

  <div class="box">
    <table>
      <thead>
        <tr>
          <th>Address</th>
          <th>Amount</th>
        </tr>
      </thead>
      <tbody>
        {#each loopObjArr as obj}
          <tr>
            <td>{obj.address}</td>
            <td>{obj.amount}</td>
          </tr>
        {/each}
      </tbody>
    </table>
  </div>
{/if}

